<template>
  <div id="wrap">
    <div class="header">
      <span class="status" :class="{ confirm: orderForm.status }"></span>
      <span class="time">{{ orderForm.date }}</span>
      <!--<div type="button">联系用户</div>-->
    </div>
    <ul class="col-1">
      <li>用户ID</li>
      <li>用户名</li>
      <li>订单ID</li>
      <li>订单号</li>
      <li>菜园名</li>
      <li>类型</li>
      <li>面积</li>
      <li>价格</li>
    </ul>
    <ul class="col-2">
      <li>{{ orderForm.userId }}</li>
      <li>{{ orderForm.userName }}</li>
      <li>{{ orderForm.orderId }}</li>
      <li>{{ orderForm.orderNum }}</li>
      <li>{{ orderForm.gardenName }}</li>
      <li>{{ orderForm.type==1 ? '半管理':'全管理' }}</li>
      <li>{{ orderForm.area }}</li>
      <li>{{ orderForm.price }}</li>
    </ul>
    <div class="confirm"><a href="javascript:;">确认已处理订单</a></div>
  </div>
</template>

<script>
  export default {
    name: 'orderformTable',
    props: ['orderForm']
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #wrap{
    width: 992px;
    height: 178px;
    margin: 0 auto;
    position: relative;
    box-sizing: border-box;
    border: 1px solid #ececec;
    margin-bottom: 20px;
    transition: .2s;
  }
  #wrap:hover{
    border: 1px solid #00ca79;
  }
  .header{
    height: 48px;
    background-color: #f1f1f1;
    position: relative;
  }
  .header .status{
    display: inline-block;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: #ff9900;
    position: absolute;
    left: 20px;
    top: 15px;
  }
  .header .confirm{
    background-color: #00ca79;
  }
  .header .time{
    position: absolute;
    left: 70px;
    top: 13px;
    font-weight: bold;
  }
  .header div{
    position: absolute;
    width: 70px;
    height: 24px;
    line-height: 24px;
    text-align: center;
    font-size: 14px;
    right: 20px;
    top: 15px;
    border: 1px solid #00ca79;
    border-radius: 5px;
    background-color: #00ca79;
    color: #fff;
    font-weight: bold;
  }
  ul li{
    float: left;
    width: 100px;
    height: 100%;
    box-sizing: border-box;
    text-align: center;
    transition: .4s;
  }
  ul li:nth-of-type(1){
    width: 66px;
  }
  ul li:nth-of-type(4){
    width: 146px;
  }
  ul li:hover{
    background-color: #f1f1f1;
  }
  ul:nth-of-type(1){
    width: 812px;
    height: 40px;
    line-height: 40px;
  }
  ul:nth-of-type(1) li{
    border-right: 1px solid #ececec;
    border-bottom: 1px solid #ececec;
  }
  ul:nth-of-type(2){
    width: 812px;
    height: 88px;
    line-height: 88px;
  }
  ul:nth-of-type(2) li{
    border-right: 1px solid #ececec;
  }
  .confirm{
    position: absolute;
    width: 179px;
    height: 128px;
    line-height: 128px;
    text-align: center;
    right: 0px;
    bottom: 0px;
    transition: .3s;
  }
  .confirm:hover{
    background-color: #ccc;
  }
  .confirm a{
    padding: 12px 10px;
    background-color: #00ca79;
    color: #fff;
    font-weight: bold;
    border-radius: 10px;
  }
</style>



