<template>
  <div class="content">
    <div class="loading" v-if="dataOk">
      loading...
    </div>
    <div class="loading" v-else-if="dataError">
      请求出错...
    </div>
    <orderform-table
      v-for="orderForm in orderForms"
      :key="orderForm.id"
      :orderForm="orderForm"></orderform-table>
    <paging></paging>
  </div>
</template>

<script>
  import orderformTable from './orderform-table'
  import paging from '../others/paging'
  export default {
    name: 'orderformComplete',
    data () {
      return {
        dataOk: true,
        dataError: false,
        orderForms:[]
      }
    },
    components: {
      orderformTable,
      paging
    },
    created () {
      // 组件创建完后获取数据
      this.fetchData()
    },
    /*watch: {
     // 如果路由有变化，会再次执行该方法
     '$route': 'fetchData'
     },*/
    methods: {
      fetchData(){
        this.$http.get('static/orderFormComplete.json')
          .then(res => {
            this.orderForms = res.data;
            this.dataOk = false
            console.log(this.orderForms);
          }, res => {
            this.dataOk = false
            this.dataError = true;
          })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .loading{
    width: 68px;
    height: 20px;
    margin: 0 auto;
    position: absolute;
    left: 480px;
    top: 100px;
  }
</style>



